import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'
import type { 
  AvailableSlot, 
  AvailabilityResponse, 
  AvailabilityStatus,
  TimeBlock,
  CheckAvailabilityResponse
} from '@/lib/calendar/types'

// ==========================================
// GET /api/v1/calendars/[calendarId]/availability
// Get available time slots for booking
// ==========================================
export async function GET(
  request: NextRequest,
  { params }: { params: Promise<{ calendarId: string }> }
) {
  try {
    const { calendarId } = await params
    const supabase = supabaseAdmin
    const { searchParams } = new URL(request.url)
    
    const startDate = searchParams.get('startDate')
    const endDate = searchParams.get('endDate')
    const timezone = searchParams.get('timezone')
    const duration = searchParams.get('duration')
    const appointmentTypeId = searchParams.get('appointmentTypeId')

    // Validation
    if (!startDate || !endDate || !timezone) {
      return NextResponse.json(
        { success: false, error: 'startDate, endDate, and timezone are required' },
        { status: 400 }
      )
    }

    // Get calendar with working hours
    const { data: calendar, error: calError } = await supabase
      .from('calendars')
      .select(`
        *,
        calendar_working_hours (*),
        calendar_date_overrides (*),
        calendar_members (
          user_id,
          is_active,
          priority_order,
          weight
        )
      `)
      .eq('id', calendarId)
      .eq('status', 'active')
      .single()

    if (calError || !calendar) {
      return NextResponse.json(
        { success: false, error: 'Calendar not found' },
        { status: 404 }
      )
    }

    // Get appointment type if specified
    let appointmentDuration = calendar.duration_minutes
    let appointmentBuffer = {
      before: calendar.buffer_before_minutes,
      after: calendar.buffer_after_minutes
    }

    if (appointmentTypeId) {
      const { data: appointmentType } = await supabase
        .from('appointment_types')
        .select('*')
        .eq('id', appointmentTypeId)
        .eq('calendar_id', calendarId)
        .single()

      if (appointmentType) {
        appointmentDuration = appointmentType.duration_minutes
        appointmentBuffer = {
          before: appointmentType.buffer_before_minutes || calendar.buffer_before_minutes,
          after: appointmentType.buffer_after_minutes || calendar.buffer_after_minutes
        }
      }
    }

    // Override duration if provided
    if (duration) {
      appointmentDuration = parseInt(duration)
    }

    // Get existing appointments in date range
    const { data: existingAppointments } = await supabase
      .from('appointments')
      .select('id, start_time, end_time, assigned_to, status')
      .eq('calendar_id', calendarId)
      .gte('start_time', new Date(startDate).toISOString())
      .lte('end_time', new Date(new Date(endDate).setHours(23, 59, 59, 999)).toISOString())
      .in('status', ['scheduled', 'confirmed'])

    // Get external calendar events if integrations exist
    const { data: integrations } = await supabase
      .from('calendar_integrations')
      .select('id')
      .eq('calendar_id', calendarId)
      .eq('is_active', true)

    let externalEvents: any[] = []
    if (integrations && integrations.length > 0) {
      const integrationIds = integrations.map(i => i.id)
      const { data: events } = await supabase
        .from('external_calendar_events')
        .select('*')
        .in('integration_id', integrationIds)
        .gte('start_time', new Date(startDate).toISOString())
        .lte('end_time', new Date(new Date(endDate).setHours(23, 59, 59, 999)).toISOString())

      externalEvents = events || []
    }

    // Generate available slots
    const slots = generateAvailableSlots({
      calendar,
      startDate,
      endDate,
      timezone,
      duration: appointmentDuration,
      bufferBefore: appointmentBuffer.before,
      bufferAfter: appointmentBuffer.after,
      existingAppointments: existingAppointments || [],
      externalEvents,
      slotInterval: calendar.slot_interval_minutes,
      minimumNoticeHours: calendar.minimum_notice_hours,
      maximumBookingDays: calendar.maximum_booking_days
    })

    // Generate days summary
    const daysSummary = generateDaysSummary(slots, startDate, endDate)

    const response: AvailabilityResponse = {
      calendarId,
      timezone,
      slots,
      daysSummary
    }

    return NextResponse.json({
      success: true,
      data: response
    })

  } catch (error) {
    console.error('Error in GET /api/v1/calendars/[calendarId]/availability:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// POST /api/v1/calendars/[calendarId]/availability/check
// Check if specific time slot is available
// ==========================================
export async function POST(
  request: NextRequest,
  { params }: { params: Promise<{ calendarId: string }> }
) {
  try {
    const { calendarId } = await params
    const supabase = supabaseAdmin
    const body = await request.json()

    const { startTime, endTime } = body

    if (!startTime || !endTime) {
      return NextResponse.json(
        { success: false, error: 'startTime and endTime are required' },
        { status: 400 }
      )
    }

    // Get calendar
    const { data: calendar, error: calError } = await supabase
      .from('calendars')
      .select(`
        *,
        calendar_working_hours (*),
        calendar_date_overrides (*)
      `)
      .eq('id', calendarId)
      .eq('status', 'active')
      .single()

    if (calError || !calendar) {
      return NextResponse.json(
        { success: false, error: 'Calendar not found' },
        { status: 404 }
      )
    }

    const slotStart = new Date(startTime)
    const slotEnd = new Date(endTime)
    const conflicts: CheckAvailabilityResponse['conflicts'] = []

    // 1. Check minimum notice period
    const now = new Date()
    const hoursUntilSlot = (slotStart.getTime() - now.getTime()) / (1000 * 60 * 60)
    if (hoursUntilSlot < calendar.minimum_notice_hours) {
      conflicts.push({
        type: 'outside_hours',
        description: `Minimum ${calendar.minimum_notice_hours} hours notice required`
      })
    }

    // 2. Check maximum advance booking
    const daysUntilSlot = (slotStart.getTime() - now.getTime()) / (1000 * 60 * 60 * 24)
    if (daysUntilSlot > calendar.maximum_booking_days) {
      conflicts.push({
        type: 'outside_hours',
        description: `Cannot book more than ${calendar.maximum_booking_days} days in advance`
      })
    }

    // 3. Check working hours
    const dayOfWeek = slotStart.getDay()
    const workingHours = calendar.calendar_working_hours?.find(
      (wh: any) => wh.day_of_week === dayOfWeek
    )

    if (!workingHours?.is_available) {
      conflicts.push({
        type: 'outside_hours',
        description: 'Selected day is not a working day'
      })
    } else {
      const slotTimeStr = slotStart.toTimeString().slice(0, 5) // HH:mm
      const slotEndTimeStr = slotEnd.toTimeString().slice(0, 5)
      const isWithinWorkingHours = workingHours.time_blocks.some((block: TimeBlock) => {
        return slotTimeStr >= block.startTime && slotEndTimeStr <= block.endTime
      })
      
      if (!isWithinWorkingHours) {
        conflicts.push({
          type: 'outside_hours',
          description: 'Selected time is outside working hours'
        })
      }
    }

    // 4. Check date overrides
    const dateStr = slotStart.toISOString().split('T')[0]
    const dateOverride = calendar.calendar_date_overrides?.find(
      (d: any) => d.override_date === dateStr
    )
    if (dateOverride?.override_type === 'blocked' || dateOverride?.override_type === 'holiday') {
      conflicts.push({
        type: 'blocked_date',
        description: dateOverride.reason || 'Date is blocked'
      })
    }

    // 5. Check existing appointments
    const bufferBefore = calendar.buffer_before_minutes * 60 * 1000
    const bufferAfter = calendar.buffer_after_minutes * 60 * 1000

    const { data: existingAppointments } = await supabase
      .from('appointments')
      .select('id, start_time, end_time, status')
      .eq('calendar_id', calendarId)
      .in('status', ['scheduled', 'confirmed'])
      .or(`start_time.lte.${slotEnd.toISOString()},end_time.gte.${slotStart.toISOString()}`)

    if (existingAppointments) {
      for (const apt of existingAppointments) {
        const aptStart = new Date(apt.start_time).getTime() - bufferBefore
        const aptEnd = new Date(apt.end_time).getTime() + bufferAfter

        if (slotStart.getTime() < aptEnd && slotEnd.getTime() > aptStart) {
          conflicts.push({
            type: 'existing_appointment',
            startTime: apt.start_time,
            endTime: apt.end_time
          })
        }
      }
    }

    // 6. Check external calendar events
    const { data: integrations } = await supabase
      .from('calendar_integrations')
      .select('id')
      .eq('calendar_id', calendarId)
      .eq('is_active', true)

    if (integrations && integrations.length > 0) {
      const integrationIds = integrations.map(i => i.id)
      const { data: externalEvents } = await supabase
        .from('external_calendar_events')
        .select('*')
        .in('integration_id', integrationIds)
        .or(`start_time.lte.${slotEnd.toISOString()},end_time.gte.${slotStart.toISOString()}`)

      if (externalEvents) {
        for (const event of externalEvents) {
          const eventStart = new Date(event.start_time).getTime()
          const eventEnd = new Date(event.end_time).getTime()

          if (slotStart.getTime() < eventEnd && slotEnd.getTime() > eventStart) {
            conflicts.push({
              type: 'external_event',
              startTime: event.start_time,
              endTime: event.end_time,
              description: event.summary
            })
          }
        }
      }
    }

    const isAvailable = conflicts.length === 0

    // Find next available slot if not available
    let nextAvailableSlot: AvailableSlot | undefined
    if (!isAvailable) {
      // Simple implementation: find next slot on same day
      const slots = generateAvailableSlots({
        calendar,
        startDate: dateStr,
        endDate: dateStr,
        timezone: calendar.timezone,
        duration: calendar.duration_minutes,
        bufferBefore: calendar.buffer_before_minutes,
        bufferAfter: calendar.buffer_after_minutes,
        existingAppointments: existingAppointments || [],
        externalEvents: [],
        slotInterval: calendar.slot_interval_minutes,
        minimumNoticeHours: calendar.minimum_notice_hours,
        maximumBookingDays: calendar.maximum_booking_days
      })

      nextAvailableSlot = slots.find(s => 
        new Date(s.utcStart).getTime() > slotStart.getTime() && s.available
      )
    }

    const response: CheckAvailabilityResponse = {
      available: isAvailable,
      conflicts: isAvailable ? undefined : conflicts,
      nextAvailableSlot
    }

    return NextResponse.json({
      success: true,
      data: response
    })

  } catch (error) {
    console.error('Error in POST /api/v1/calendars/[calendarId]/availability:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// HELPER FUNCTIONS
// ==========================================

interface GenerateSlotsOptions {
  calendar: any
  startDate: string
  endDate: string
  timezone: string
  duration: number
  bufferBefore: number
  bufferAfter: number
  existingAppointments: any[]
  externalEvents: any[]
  slotInterval: number
  minimumNoticeHours: number
  maximumBookingDays: number
}

function generateAvailableSlots(options: GenerateSlotsOptions): AvailableSlot[] {
  const {
    calendar,
    startDate,
    endDate,
    timezone,
    duration,
    bufferBefore,
    bufferAfter,
    existingAppointments,
    externalEvents,
    slotInterval,
    minimumNoticeHours,
    maximumBookingDays
  } = options

  const slots: AvailableSlot[] = []
  const now = new Date()
  const minBookingTime = new Date(now.getTime() + minimumNoticeHours * 60 * 60 * 1000)
  const maxBookingTime = new Date(now.getTime() + maximumBookingDays * 24 * 60 * 60 * 1000)

  // Create blocked time ranges from existing appointments
  const blockedRanges = [
    ...existingAppointments.map(apt => ({
      start: new Date(apt.start_time).getTime() - bufferBefore * 60 * 1000,
      end: new Date(apt.end_time).getTime() + bufferAfter * 60 * 1000
    })),
    ...externalEvents.map(event => ({
      start: new Date(event.start_time).getTime(),
      end: new Date(event.end_time).getTime()
    }))
  ]

  // Get date overrides map
  const dateOverridesMap = new Map<string, any>()
  calendar.calendar_date_overrides?.forEach((d: any) => {
    dateOverridesMap.set(d.override_date, d)
  })

  // Iterate through each day
  let currentDate = new Date(startDate)
  const end = new Date(endDate)

  while (currentDate <= end) {
    const dateStr = currentDate.toISOString().split('T')[0]
    const dayOfWeek = currentDate.getDay()

    // Check date override
    const dateOverride = dateOverridesMap.get(dateStr)
    if (dateOverride?.override_type === 'blocked' || dateOverride?.override_type === 'holiday') {
      currentDate.setDate(currentDate.getDate() + 1)
      continue
    }

    // Get working hours for this day
    let timeBlocks: TimeBlock[] = []
    
    if (dateOverride?.override_type === 'available' && dateOverride?.custom_hours) {
      // Use custom hours from override
      timeBlocks = dateOverride.custom_hours
    } else {
      // Use regular working hours
      const workingHours = calendar.calendar_working_hours?.find(
        (wh: any) => wh.day_of_week === dayOfWeek
      )
      if (workingHours?.is_available) {
        timeBlocks = workingHours.time_blocks || []
      }
    }

    // Generate slots for each time block
    for (const block of timeBlocks) {
      const [startHour, startMin] = block.startTime.split(':').map(Number)
      const [endHour, endMin] = block.endTime.split(':').map(Number)

      const blockStart = new Date(currentDate)
      blockStart.setHours(startHour, startMin, 0, 0)

      const blockEnd = new Date(currentDate)
      blockEnd.setHours(endHour, endMin, 0, 0)

      let slotStart = new Date(blockStart)

      while (slotStart.getTime() + duration * 60 * 1000 <= blockEnd.getTime()) {
        const slotEnd = new Date(slotStart.getTime() + duration * 60 * 1000)

        // Check if slot is bookable (within notice period and max booking window)
        const isAfterMinNotice = slotStart.getTime() >= minBookingTime.getTime()
        const isBeforeMaxBooking = slotStart.getTime() <= maxBookingTime.getTime()

        // Check if slot conflicts with any blocked range
        const hasConflict = blockedRanges.some(range => {
          return slotStart.getTime() < range.end && slotEnd.getTime() > range.start
        })

        const isAvailable = isAfterMinNotice && isBeforeMaxBooking && !hasConflict

        slots.push({
          startTime: formatToTimezone(slotStart, timezone),
          endTime: formatToTimezone(slotEnd, timezone),
          utcStart: slotStart.toISOString(),
          utcEnd: slotEnd.toISOString(),
          available: isAvailable
        })

        // Move to next slot
        slotStart = new Date(slotStart.getTime() + slotInterval * 60 * 1000)
      }
    }

    currentDate.setDate(currentDate.getDate() + 1)
  }

  return slots
}

function generateDaysSummary(
  slots: AvailableSlot[], 
  startDate: string, 
  endDate: string
): { date: string; availableSlots: number; status: AvailabilityStatus }[] {
  const summary: { date: string; availableSlots: number; status: AvailabilityStatus }[] = []
  
  // Group slots by date
  const slotsByDate = new Map<string, AvailableSlot[]>()
  
  slots.forEach(slot => {
    const date = slot.utcStart.split('T')[0]
    if (!slotsByDate.has(date)) {
      slotsByDate.set(date, [])
    }
    slotsByDate.get(date)!.push(slot)
  })

  // Generate summary for each day
  let currentDate = new Date(startDate)
  const end = new Date(endDate)

  while (currentDate <= end) {
    const dateStr = currentDate.toISOString().split('T')[0]
    const daySlots = slotsByDate.get(dateStr) || []
    const availableCount = daySlots.filter(s => s.available).length

    let status: AvailabilityStatus = 'none'
    if (availableCount > 5) {
      status = 'many'
    } else if (availableCount > 0) {
      status = 'few'
    }

    summary.push({
      date: dateStr,
      availableSlots: availableCount,
      status
    })

    currentDate.setDate(currentDate.getDate() + 1)
  }

  return summary
}

function formatToTimezone(date: Date, timezone: string): string {
  // Simple implementation - in production use a proper timezone library like date-fns-tz
  try {
    return date.toLocaleString('en-US', { 
      timeZone: timezone,
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit',
      second: '2-digit',
      hour12: false
    }).replace(/(\d+)\/(\d+)\/(\d+), /, '$3-$1-$2T') + ':00'
  } catch {
    return date.toISOString()
  }
}
